
import React, { useState } from 'react'
import MenuItem from './MenuItem'
import MenuButton from './MenuButton'
import Menu from './Menu'


export default function Burger() {
  const [menuOpen, setMenuOpen] = useState(false)
  const handleMenuClick = () => {
    setMenuOpen(!menuOpen)
  }
  const handleLinkClick = () => {
    setMenuOpen(true)
  }
  const menu = ['Home', 'Our Games', 'Our Team', 'Contact']
  const menuItems = menu.map((val, index) => {
    return (
      <MenuItem key={index} onClick={handleLinkClick}>
        {val}
      </MenuItem>
    )
  });

  return (
    <div>
      <div className="main">
        <div className ="menu_button">
        <MenuButton
            open={menuOpen}
            clickMenu={handleMenuClick}
        />
        </div>
        <Menu  open={menuOpen}>
          {menuItems}
        </Menu>
      </div>

      <style jsx>{`
        .main{
          display: flex;
          flex-direction: column;
          align-items: center;
          height: 100vh;
        }
        .menu_button {
          position: absolute;
          margin-top: 0;
          margin-left: 0;
          z-index: 99;
          opacity: 0.9;
          display: flex;
          align-items: center;
          background-color: black;
          width: 100%;
          color: white;
        }
    `}</style>
    </div>
  )
}